header{
	background:linear-gradient(0deg, rgba(32, 32, 32, 0.8), rgba(32, 32, 32, 0.8)), url('images/hero.jpg') fixed;
	background-position:center;
	background-size:cover;

	.container{
		display:flex;
		flex-direction:column;
		justify-content:space-between;
		height:100vh;
	}

	.main-nav{
		display:flex;
		justify-content:space-between;
		align-items:center;
		padding:60px 0;

		#logo{
			color:white;
			text-transform: uppercase;
			border:2px solid white;
			padding:10px 20px;
			border-radius:2px;
			transition: all 0.5s ease;
			&:hover{
				border:2px solid $red;
				background:$red;
			}
			&:focus{
				border:2px solid $red;
				background:$red;
			}
		}

		a{
			&:hover{
					color:$red;
			}
			&:focus{
				color:$red;
			}
		}
	}

	.hero-text{
		text-align:center;

		h1{
			margin-bottom:35px;
		}

		h6.hero-tag-line{
			color:$light-p-grey;
			max-width:600px;
			margin:0 auto;
			margin-bottom:100px;
		}

		.line-wrapper{
			width:100px;
			margin:0 auto;
			margin-bottom:35px;
			content:'';
		}

		.upperline{
			border-bottom:2px solid #FF261C;
			width:80px;
			margin-bottom:5px;
			float:left;
		}

		.lowerline{
			border-bottom:2px solid #FF261C;
			width:80px;
			float:right;
		}

		button{
			border:2px solid white;
			background:rgba(0,0,0,0);
			color:white;
			padding:10px 20px;
			border-radius:2px;
			transition: all 0.5s ease;
			
			&:hover{
				border:2px solid $red;
				background:$red;
			}
			&:focus{
				border:2px solid $red;
				background:$red;
			}
		}
	}

	svg {
	  display: block;
	  width: 100%;
	  height: 90px;
	  background: transparent;
	  fill:$bg-grey;
	}
}